import { useState, useEffect } from 'react';
import { useSwipeable } from 'react-swipeable';

import Player from '../components/Player/Player';
import Chat from '../components/Chat/Chat';
import SwipeHints from '../components/SwipeHints';

import Message from '../api/Message';

import {
	IMessage,
	TVideoDataState,
	TSetSubtitles,
	TUserData,
} from '../types/types';

const MainPage = () => {
	const [messages, setMessages] = useState<IMessage[]>([]);
	const [chatActions, setChatActions] = useState<string[]>([]);
	const [isMessageFromScript, setIsMessageFromScript] =
		useState<boolean>(false);
	const [messageScriptPosition, setMessageSciptPosition] = useState<number>(0);
	const [scriptIndex, setScriptIndex] = useState<number>(0);
	const [playerData, setPlayerData] = useState<TVideoDataState>({});
	const [playerLanguage, setPlayerLanguage] = useState<string>('');
	const [isChatActive, setIsChatActive] = useState<boolean>(false);
	const [overlayActive, setOverlayActive] = useState<boolean>(true);

	useEffect(() => {
		Message.sendAuthMessage()
			.then((data) => setPlayerData(data ?? {}))
			.catch((err) => console.log('MainPage sendInitialMessage error: ', err));
	}, []);

	useEffect(() => {
		const actionButtons = playerData.actions?.buttons;

		if (actionButtons) {
			const actions = actionButtons.reduce((acc: string[], action: any) => {
				if (!action[playerLanguage]) return acc;

				acc.push(action[playerLanguage]);
				return acc;
			}, []);

			setChatActions(actions);
		}
	}, [playerData, playerLanguage]);

	useEffect(() => {
		const script = playerData.actions?.script;
		const endOfScript = playerData.script_end;
		const isScriptChanged = !!playerData.change_script;

		if (script?.length && !endOfScript && !isMessageFromScript) {
			setIsMessageFromScript(true);
			setScriptIndex(script[0]);
		}

		console.log('isScriptChanged: ', isScriptChanged, playerData.change_script);

		if (isScriptChanged) {
			setScriptIndex(playerData.change_script as number);
		}

		if (endOfScript) {
			setIsMessageFromScript(false);
			setMessageSciptPosition(0);
		}
	}, [playerData]);

	const swipeHandlers = useSwipeable({
		onSwipedUp: () => setIsChatActive(true),
	});

	const overlaySwipeHandlers = useSwipeable({
		onSwiping: () => setOverlayActive(false),
	});

	const sendMessage = ({ text, fromMe }: IMessage) => {
		setMessages((prevState) => [...prevState, { text, fromMe }]);

		if (isMessageFromScript) {
			const position = messageScriptPosition + 1;
			setMessageSciptPosition(position);

			Message.sendScriptMessage({ msg: text, pk: scriptIndex, position })
				.then((data) => setPlayerData(data ?? {}))
				.catch((err) => console.log('MainPage sendMessage error: ', err));
		} else {
			Message.sendMessage({ msg: text })
				.then((data) => setPlayerData(data ?? {}))
				.catch((err) => console.log('MainPage sendMessage error: ', err));
		}
	};

	const setSubtitles: TSetSubtitles = (text) => {
		setMessages((prevState) => [...prevState, { text, fromMe: false }]);
	};

	const sendUserData = async (value: TUserData) => {
		Message.sendUserData(value)
			.then(() => {
				// TODO: Send sendGreetingMessage after successful sendUserData response
				Message.sendGreetingMessage()
					.then((data) => setPlayerData(data ?? {}))
					.catch((err) =>
						console.log('MainPage sendInitialMessage error: ', err)
					);
			})
			.catch((err) => console.log('MainPage sendInitialMessage error: ', err));
	};

	const onPlayerLanguageChange = (value: string) => {
		setPlayerLanguage(value);

		// TODO: Move this logic to useEffect hook
		if (!playerData?.actions?.subtitles?.[value]) return;

		const text = playerData.actions.subtitles[value];
		setSubtitles(text);
	};

	return (
		<div className="main-page" {...swipeHandlers}>
			<Player
				data={playerData}
				onPlayerLanguageChange={onPlayerLanguageChange}
			/>

			<Chat
				className="main-page__chat"
				messages={messages}
				actions={chatActions}
				onSendMessage={sendMessage}
				onSendUserData={sendUserData}
				isChatActive={isChatActive}
				setIsChatActive={setIsChatActive}
			/>

			<SwipeHints
				overlayActive={overlayActive}
				swipeHandlers={{ ...overlaySwipeHandlers }}
			/>
		</div>
	);
};

export default MainPage;
