import React from 'react';

import BaseBtnAlpha from '../UI/buttons/BaseBtnAlpha/BaseBtnAlpha';
import AppSvg from '../UI/AppSvg/AppSvg';

import spritePath from '../../assets/svg/sprite.svg';

export interface IChatToggleProps {
	className?: string;
	setIsChatActive: (value: boolean) => void;
	isChatActive: boolean;
}

const ChatToggle: React.FC<IChatToggleProps> = (
	{ className, setIsChatActive, isChatActive } = {
		className: '',
		setIsChatActive: () => null,
		isChatActive: false,
	}
) => {
	return (
		<div
			className={['chat-toggle', className].join(' ')}
			onMouseLeave={() => setIsChatActive(false)}
		>
			<BaseBtnAlpha
				className="chat__toggle-chat-btn"
				callOnClick={() => setIsChatActive(!isChatActive)}
				callOnHover={() => setIsChatActive(true)}
				active={false}
				aria={{
					label: 'Open chat',
					haspopup: 'dialog',
					expanded: isChatActive,
				}}
			>
				<AppSvg
					iconName="chat"
					width="40"
					height="40"
					viewBox="0 0 50 50"
					spritePath={spritePath}
				/>
			</BaseBtnAlpha>
		</div>
	);
};

export default ChatToggle;
