import { useState, FC, useEffect } from 'react';
import { CSSTransition } from 'react-transition-group';
import { useSwipeable } from 'react-swipeable';

import ChatHeader from './ChatHeader';
import ChatHistory from './ChatHistory';
import ChatForm from './ChatForm';
import ChatToggle from './ChatToggle';

import { IMessage } from '../../types/types';
import { FORM_SEND_TYPES, TFormSendType, TUserData } from './types';

export interface IChatProps {
	messages: IMessage[];
	onSendMessage: (msg: IMessage) => void;
	onSendUserData: (userData: TUserData) => void;
	isChatActive: boolean;
	setIsChatActive: (value: boolean) => void;
	className?: string;
	actions: string[];
}

const Chat: FC<IChatProps> = ({
	messages,
	onSendMessage,
	onSendUserData,
	isChatActive,
	setIsChatActive,
	className,
	actions,
}) => {
	const [isUserSentData, setIsUserSentData] = useState<boolean>(false);

	const swipeHandlers = useSwipeable({
		onSwipedDown: () => setIsChatActive(false),
	});

	const sendMessage = (text: string) => {
		onSendMessage({ text, fromMe: true });
	};

	const sendUserData = (data: TUserData) => {
		onSendUserData(data);
		setTimeout(() => setIsUserSentData(true));
	};

	const onSend = ({
		value,
		type,
	}: {
		value: TUserData | string;
		type: TFormSendType;
	}) => {
		switch (type) {
			case FORM_SEND_TYPES.MESSAGE:
				sendMessage(value as string);
				break;

			case FORM_SEND_TYPES.USER_DATA:
				sendUserData(value as TUserData);
				break;
		}
	};

	return (
		<div className={['chat', className].join(' ')} {...swipeHandlers}>
			<CSSTransition
				in={isChatActive}
				timeout={{
					exit: 400,
				}}
				classNames="popup-animation"
				mountOnEnter
				unmountOnExit
			>
				<div
					className="chat__wrapper"
					onMouseLeave={() => setIsChatActive(false)}
					onMouseEnter={() => setIsChatActive(true)}
				>
					<ChatHeader />

					<ChatHistory className="chat__history" messages={messages} />

					<ChatForm
						className="chat__form"
						actions={actions}
						onSend={onSend}
						isUserSentData={isUserSentData}
					/>
				</div>
			</CSSTransition>

			<ChatToggle
				className={'chat__toggle'}
				setIsChatActive={setIsChatActive}
				isChatActive={isChatActive}
			/>
		</div>
	);
};

export default Chat;
