import React, { FC } from 'react';
import { IAria } from '../../../../types/types';

import classes from './BaseBtnAlpha.module.scss';

export interface IBaseBtnAlphaProps {
	children: React.ReactNode;
	callOnClick: () => void;
	callOnHover?: () => void;
	aria: IAria;
	active?: boolean;
	className?: string;
}

const BaseBtnAlpha: FC<IBaseBtnAlphaProps> = (
	{ children, callOnHover, callOnClick, aria, active, className } = {
		children: '',
		callOnClick: () => null,
		callOnHover: () => null,
		active: false,
		className: '',
		aria: {
			label: 'Button',
			haspopup: false,
			expanded: false,
		},
	}
) => {
	return (
		<button
			onMouseEnter={callOnHover}
			onClick={callOnClick}
			className={[
				classes.BaseBtnAlpha,
				active ? classes.BaseBtnAlphaActive : '',
				className,
			].join(' ')}
			aria-label={aria.label}
			aria-haspopup={aria.haspopup}
			aria-expanded={aria.expanded}
		>
			{children}
		</button>
	);
};

export default BaseBtnAlpha;
