import { FC, useEffect, useRef, useState } from 'react';

import LoaderCircle from '../UI/loaders/LoaderCircle';
import {
	IVideoAttributes,
	TSaveVideoAttributes,
	TVideoAttributesKey,
} from '../../types/types';

export interface IPlayerVideoProps {
	className: string;
	attributes: IVideoAttributes;
	onSaveAttributes: TSaveVideoAttributes;
	saveCurrentTime: boolean;
	isPlayAgain: boolean;
	setIsPlayAgain: (value: boolean) => void;
}

const PlayerVideo: FC<IPlayerVideoProps> = ({
	className,
	attributes,
	onSaveAttributes,
	saveCurrentTime,
	isPlayAgain,
	setIsPlayAgain,
}) => {
	const [isPaused, setIsPaused] = useState<boolean>(false);
	const [isLoading, setIsLoading] = useState<boolean>(true);

	const videoRef = useRef<HTMLVideoElement>(null);

	useVideoAttributes({
		videoRef,
		attributes,
		onSaveAttributes,
		saveCurrentTime,
	});

	useEffect(() => {
		if (isPlayAgain) {
			onSaveAttributes({ currentTime: 0 });

			if (videoRef.current) {
				videoRef.current.currentTime = 0;
			}

			playVideo();
			setIsPlayAgain(false);
		}
	}, [isPlayAgain]);

	function playVideo() {
		const startPlayPromise = videoRef.current?.play();

		if (startPlayPromise !== undefined) {
			startPlayPromise
				.then(() => {
					setIsPaused(false);
				})
				.catch((err) => {
					if (err.name === 'NotAllowedError') {
						setIsPaused(true);
					} else if (err.name === 'NotSupportedError') {
						console.log('Your browser does not support this video player');
					}
				});
		}
	}

	const onSuspend = () => {
		playVideo();
	};

	const onPlay = () => {
		setIsPaused(false);
	};

	const onWaiting = () => {
		setIsLoading(true);
	};

	const onPlaying = () => {
		setIsLoading(false);
	};

	const onCanPlay = () => {
		setIsLoading(false);
		playVideo();
	};

	const onEmptied = () => {
		setIsLoading(true);
	};

	return (
		<div className={[className, 'player-video'].join(' ')}>
			<video
				ref={videoRef}
				// Try to play video if autoplay is blocked by browser
				onSuspend={onSuspend}
				onPlay={onPlay}
				onWaiting={onWaiting}
				onPlaying={onPlaying}
				onCanPlay={onCanPlay}
				onCanPlayThrough={onCanPlay}
				onEmptied={onEmptied}
				className="player-video__video"
			/>

			{isPaused && (
				<button
					onClick={playVideo}
					aria-label="Play video"
					className="player-video__play-btn"
				>
					&#9658;
				</button>
			)}

			{isLoading && <LoaderCircle />}
		</div>
	);
};

function useVideoAttributes({
	videoRef,
	attributes,
	onSaveAttributes,
	saveCurrentTime,
}: {
	videoRef: React.RefObject<HTMLVideoElement>;
	attributes: IVideoAttributes;
	onSaveAttributes: TSaveVideoAttributes;
	saveCurrentTime: boolean;
}) {
	useEffect(() => {
		Object.keys(attributes).map((key) => {
			const typedKey = key as TVideoAttributesKey;

			if (attributes[typedKey] && typeof attributes[typedKey] === 'boolean') {
				videoRef.current?.setAttribute(key, '');
			}
		});
	}, []);

	useEffect(() => {
		if (videoRef.current) {
			videoRef.current.volume = attributes.volume;
		}
	}, [attributes.volume]);

	useEffect(() => {
		if (videoRef.current) {
			onSaveAttributes({ currentTime: videoRef.current.currentTime });

			videoRef.current.src = attributes.src;
			videoRef.current.poster = attributes.poster;
		}
	}, [attributes.src]);

	useEffect(() => {
		if (videoRef.current && saveCurrentTime) {
			videoRef.current.currentTime = attributes.currentTime;
		}
	}, [attributes.currentTime]);
}

export default PlayerVideo;
