import { FC } from 'react';

import AppSvg from '../../AppSvg/AppSvg';
import spritePath from '../../../../assets/svg/sprite.svg';
import classes from './VolumeControl.module.scss';

export interface IVolumeControlProps {
	value: number;
	min: number;
	max: number;
	step: number;
	callOnInput: (value: number) => void;
}

const VolumeControl: FC<IVolumeControlProps> = (
	{ value, min, max, step, callOnInput } = {
		value: 0.5,
		min: 0,
		max: 1,
		step: 0.01,
		callOnInput: () => null,
	}
) => {
	return (
		<div className={classes.VolumeControl}>
			<AppSvg
				iconName="minus"
				height="25"
				width="25"
				viewBox="2 2 22 22"
				spritePath={spritePath}
			/>

			<input
				className={classes.VolumeControlInput}
				aria-label="Volume contol"
				value={value}
				type="range"
				min={min}
				max={max}
				step={step}
				onInput={(e) =>
					callOnInput(Number((e.target as HTMLInputElement).value))
				}
			/>

			<AppSvg
				iconName="plus"
				height="25"
				width="25"
				viewBox="2 2 22 22"
				spritePath={spritePath}
			/>
		</div>
	);
};

export default VolumeControl;
