import React, { FC } from 'react';
import { SwipeableHandlers } from 'react-swipeable';

import classes from './BaseOverlay.module.scss';

export interface IBaseOverlayProps {
	className: string;
	background: string;
	children: React.ReactElement;
	swipeHandlers: SwipeableHandlers;
}
const BaseOverlay: FC<IBaseOverlayProps> = ({
	className,
	background,
	children,
	swipeHandlers,
}) => {
	return (
		<div
			{...swipeHandlers}
			style={{ background }}
			className={[classes.BaseOverlay, className].join(' ')}
		>
			{children}
		</div>
	);
};

export default BaseOverlay;
