import axios from 'axios';

import { BASE_URL } from './config';
import { TVideoURL, TVideoData } from './types';

class Video {
	static async getVideoFragment({
		lang,
		avatar,
		screen,
		quality,
		videoName,
		start,
		end,
	}: TVideoURL): Promise<TVideoData> {
		return axios
			.get(
				`${BASE_URL}media/${lang}/${avatar}/${screen}/${quality}/${videoName}/`,
				{
					params: {
						start,
						end,
					},
				}
			)
			.then((response) => {
				console.log('getVideoFragment success: ', response.data);
				return response.data;
			})
			.catch((err) => console.log('getVideoFragment error: ', err));
	}

	static getVideoUrl({
		quality,
		lang,
		avatar,
		screen,
		videoName,
		start,
		end,
	}: TVideoURL): string {
		let url = `${BASE_URL}media/${lang}/${avatar}/${screen}/${quality}/${videoName}?start=${start}`;
		if (end) url += `&end=${end}`;

		return url;
	}
}

export default Video;
