import { FC } from 'react';
import { useSwipeable } from 'react-swipeable';
import { CSSTransition } from 'react-transition-group';

import PlayerControlsToggle from './PlayerControlsToggle';
import PlayerControlsPane from './PlayerControlsPane';

import {
	TPlayerOptions,
	TVideoOptions,
	TSelectedVideoOptions,
	TSelectVideoOption,
	TSelectPlayerOption,
	TToggle,
} from '../../types/types';

export interface IPlayerControlsProps {
	className: string;
	controlsActive: boolean;
	playerOptions: TPlayerOptions;
	videoOptions: TVideoOptions;
	selectedVideoOptions: TSelectedVideoOptions;
	selectVideoOption: TSelectVideoOption;
	selectPlayerOption: TSelectPlayerOption;
	toggleControls: TToggle;
}

const PlayerControls: FC<IPlayerControlsProps> = ({
	className,
	controlsActive,
	toggleControls,
	playerOptions,
	videoOptions,
	selectedVideoOptions,
	selectVideoOption,
	selectPlayerOption,
}) => {
	// Add one more handler in order to prevent chat appearing
	const swipeUpHandler = useSwipeable({
		onSwipedUp: ({ event }) => event.stopPropagation(),
	});

	return (
		<div
			className={['player-controls', className].join(' ')}
			onMouseLeave={() => toggleControls(false)}
			{...swipeUpHandler}
		>
			<PlayerControlsToggle
				className={'player-controls__toggle-btn'}
				controlsActive={controlsActive}
				toggleControls={toggleControls}
			/>

			<CSSTransition
				in={controlsActive}
				timeout={{
					exit: 300,
				}}
				classNames="slide-animation"
				mountOnEnter
				unmountOnExit
			>
				<PlayerControlsPane
					className="player-controls__pane"
					playerOptions={playerOptions}
					videoOptions={videoOptions}
					selectedVideoOptions={selectedVideoOptions}
					selectVideoOption={selectVideoOption}
					selectPlayerOption={selectPlayerOption}
					toggleControls={toggleControls}
				/>
			</CSSTransition>
		</div>
	);
};

export default PlayerControls;
