import React from 'react';

import logo from '../../assets/png/logo.png';

const ChatHeader: React.FC = () => {
	return (
		<div className="chat-header">
			<img
				src={logo}
				alt="RuFilms logo"
				className="chat-header__logo"
				width={50}
				height={60}
			/>
		</div>
	);
};

export default ChatHeader;
