import { DEVICE_TYPES, ASPECT_RATIO } from './consts';

function getAspectRatio({
	ratios,
	deviceType,
}: {
	ratios: string[];
	deviceType: string;
}): string {
	if (!ratios || !ratios.length) return '';

	const currentAspectRatio =
		ASPECT_RATIO[deviceType] || ASPECT_RATIO[DEVICE_TYPES.DESKTOP];
	console.log(currentAspectRatio);

	if (ratios.includes(currentAspectRatio)) {
		return currentAspectRatio;
	} else {
		return ratios[0];
	}
}

export default getAspectRatio;
