import { FC } from 'react';

import ChatFormMessage from './ChatFormMessage';
import ChatFormUser from './ChatFormUser';

import { FORM_SEND_TYPES, TFormSendType, TUserData } from './types';

export interface IChatFormProps {
	className?: string;
	onSend: ({
		value,
		type,
	}: {
		value: TUserData | string;
		type: TFormSendType;
	}) => void;
	actions: string[];
	isUserSentData: boolean;
}

const ChatForm: FC<IChatFormProps> = ({
	className,
	onSend,
	actions,
	isUserSentData,
}) => {
	const sendMessage = (message: string) => {
		onSend({ value: message, type: FORM_SEND_TYPES.MESSAGE });
	};

	const sendUserData = ({ name, email }: TUserData) => {
		onSend({ value: { name, email }, type: FORM_SEND_TYPES.USER_DATA });
	};

	return (
		<div className={['chat-form', className].join(' ')}>
			{isUserSentData ? (
				<ChatFormMessage actions={actions} onSend={sendMessage} />
			) : (
				<ChatFormUser className="chat-form__user" onSend={sendUserData} />
			)}
		</div>
	);
};

export default ChatForm;
