import { FC } from 'react';
import { CSSTransition } from 'react-transition-group';

import BaseBtnAlpha from '../UI/buttons/BaseBtnAlpha/BaseBtnAlpha';
import AppSvg from '../UI/AppSvg/AppSvg';

import spritePath from '../../assets/svg/sprite.svg';
import { TToggle } from '../../types/types';

export interface IPlayerControlsToggleProps {
	className?: string;
	controlsActive: boolean;
	toggleControls: TToggle;
}

const PlayerControlsToggle: FC<IPlayerControlsToggleProps> = ({
	className,
	controlsActive,
	toggleControls,
}) => {
	const openControlsPane = () => {
		toggleControls(true);
	};

	const closeControlsPane = () => {
		toggleControls(false);
	};

	return (
		<BaseBtnAlpha
			className={className}
			callOnHover={openControlsPane}
			callOnClick={controlsActive ? closeControlsPane : openControlsPane}
			aria={{
				label: controlsActive ? 'Close pane' : 'Open pane',
				haspopup: 'menu',
				expanded: controlsActive,
			}}
			active={true}
		>
			<CSSTransition
				in={controlsActive}
				timeout={0}
				classNames={'fade-animation'}
			>
				<AppSvg
					iconName={controlsActive ? 'arrow-double-right' : 'arrow-double-left'}
					width="40"
					height="40"
					viewBox={controlsActive ? '0 0 43 43' : '4 0 43 43'}
					spritePath={spritePath}
				/>
			</CSSTransition>
		</BaseBtnAlpha>
	);
};

export default PlayerControlsToggle;
