import { FC } from 'react';

import classes from './BaseTextInput.module.scss';

enum INPUT_TYPES {
	email = 'email',
	text = 'text',
}

interface IBaseTextInputProps {
	className?: string;
	value: string;
	callOnChange: (value: string) => void;
	type: keyof typeof INPUT_TYPES;
	placeholder: string;
	required: boolean;
	autoFocus?: boolean;
}

const BaseTextInput: FC<IBaseTextInputProps> = ({
	className,
	value,
	callOnChange,
	type,
	placeholder,
	required,
	autoFocus,
}) => {
	const onChange = (value: string) => {
		callOnChange(value);
	};
	return (
		<input
			type={type}
			required={required}
			autoFocus={autoFocus}
			value={value}
			onChange={(e) => onChange(e.target.value)}
			className={[classes.BaseTextInput, className].join(' ')}
			placeholder={placeholder}
		/>
	);
};

export default BaseTextInput;
