import React, { FC } from 'react';

export interface IPlayerControlsItemProps {
	title: string;
	children: React.ReactElement | React.ReactElement[];
}
const PlayerControlsItem: FC<IPlayerControlsItemProps> = ({
	title,
	children,
}) => {
	return (
		<div className="controls-item">
			<h4 className="controls-item__title">{title}</h4>
			<div className="controls-item__container">{children}</div>
		</div>
	);
};

export default PlayerControlsItem;
