import { FC, useState } from 'react';
import { useForm, SubmitHandler } from 'react-hook-form';

import BaseBtnBeta from '../UI/buttons/BaseBtnBeta/BaseBtnBeta';

import { TUserData } from './types';

interface IChatFormUserProps {
	className: string;
	onSend: (data: TUserData) => void;
}

const ChatFormUser: FC<IChatFormUserProps> = ({ className, onSend }) => {
	const {
		register,
		handleSubmit,
		formState: { errors },
	} = useForm<TUserData>();

	const onSubmit: SubmitHandler<TUserData> = (data) => {
		console.log(data);
		onSend(data);
	};

	return (
		<form
			onSubmit={handleSubmit(onSubmit)}
			className={['chat-form-user', className].join(' ')}
		>
			{/* TODO: Create separate UI component for inputs */}
			<label className="chat-form-user__label">
				<input
					autoComplete="off"
					className="chat-form-user__input"
					{...register('name', { required: 'Enter name' })}
					placeholder="Your name"
				/>

				{errors.name && (
					<span className="chat-form-user__error">{errors.name?.message}</span>
				)}
			</label>

			<label className="chat-form-user__label">
				<input
					autoComplete="off"
					className="chat-form-user__input"
					{...register('email', {
						required: 'Enter an email',
						pattern: {
							value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
							message: 'Email must have valid format',
						},
					})}
					placeholder="Email"
				/>

				{errors.email && (
					<span className="chat-form-user__error">{errors.email?.message}</span>
				)}
			</label>

			<BaseBtnBeta
				type="submit"
				className="chat-form-user__send-btn"
				callOnClick={() => null}
				aria={{
					label: 'Send form',
					haspopup: false,
					expanded: false,
				}}
			>
				Send
			</BaseBtnBeta>
		</form>
	);
};

export default ChatFormUser;
