import { FC, useEffect, useState } from 'react';
import { CSSTransition } from 'react-transition-group';
import { SwipeableHandlers } from 'react-swipeable';
import BaseOverlay from './UI/BaseOverlay/BaseOverlay';
import AppSvg from './UI/AppSvg/AppSvg';

import spritePath from '../assets/svg/sprite.svg';

export interface ISwipeHintsProps {
	overlayActive: boolean;
	swipeHandlers: SwipeableHandlers;
}

const SwipeHints: FC<ISwipeHintsProps> = ({ overlayActive, swipeHandlers }) => {
	// TODO: remove local hintsActive, use overlayActive prop instead
	// P.S This desision was made because transition has been broken
	// after passing overlayActive prop to 'in' attribute. Local state fix it
	const [hintsActive, setHintsActive] = useState<boolean>(false);

	useEffect(() => {
		setHintsActive(overlayActive);
	}, [overlayActive]);

	const transitionTimeOutStep = 600;

	return (
		<CSSTransition
			in={overlayActive}
			timeout={{ exit: 1000 }}
			classNames="appear-animation"
			unmountOnExit
		>
			<BaseOverlay
				className="swipe-hints-overlay"
				background={'rgba(0, 0, 0, 0.8)'}
				swipeHandlers={swipeHandlers}
			>
				<div className="swipe-hints">
					<CSSTransition
						classNames="fade-slide-animation"
						in={hintsActive}
						timeout={{
							enter: transitionTimeOutStep * 5,
						}}
					>
						<p className="swipe-hints__container swipe-hints__container--3">
							<span className="swipe-hints__hint">
								Swipe anywhere in order to close this window
							</span>
						</p>
					</CSSTransition>

					<CSSTransition
						classNames="fade-slide-animation"
						in={hintsActive}
						timeout={{
							enter: transitionTimeOutStep,
						}}
					>
						<p className="swipe-hints__container swipe-hints__container--1">
							<span className="swipe-hints__hint">
								Swipe left in order to open menu
							</span>

							<AppSvg
								iconName="arrow-double-left"
								width="40"
								height="40"
								viewBox="4 0 43 43"
								spritePath={spritePath}
							/>
						</p>
					</CSSTransition>

					<CSSTransition
						classNames="fade-slide-animation"
						in={hintsActive}
						timeout={{
							enter: transitionTimeOutStep * 3,
						}}
					>
						<p className="swipe-hints__container swipe-hints__container--2">
							<span className="swipe-hints__hint">
								Swipe up in order to open chat
							</span>

							<AppSvg
								iconName="arrow-double-up"
								width="40"
								height="40"
								viewBox="5 5 50 50"
								spritePath={spritePath}
							/>
						</p>
					</CSSTransition>
				</div>
			</BaseOverlay>
		</CSSTransition>
	);
};

export default SwipeHints;
