import { FC } from 'react';

import BaseBtnBeta from '../UI/buttons/BaseBtnBeta/BaseBtnBeta';

interface IChatActionsProps {
	actions: string[];
	sendAction: (text: string) => void;
}

const ChatActions: FC<IChatActionsProps> = ({ actions, sendAction }) => {
	return (
		<div className="chat-actions">
			{actions.map((action) => (
				<BaseBtnBeta
					className="chat-actions__action"
					key={action}
					aria={{ label: `Send ${action} message` }}
					callOnClick={() => sendAction(action)}
					type="button"
				>
					{action}
				</BaseBtnBeta>
			))}
		</div>
	);
};

export default ChatActions;
