import { FC, useEffect, useState } from 'react';

import classes from './AppSvg.module.scss';

export interface IAppSvgProps {
	spritePath: string;
	iconName: string;
	className?: string;
	width: string;
	height: string;
	viewBox: string;
}

const AppSvg: FC<IAppSvgProps> = (
	{ spritePath, iconName, className, width, height, viewBox } = {
		spritePath: '',
		iconName: '',
		className: '',
		width: '40',
		height: '40',
		viewBox: '0 0 40 40',
	}
) => {
	const [iconPath, setIconPath] = useState<string>('');

	useEffect(() => {
		setIconPath(`${spritePath}#${iconName}`);
	}, [iconName]);

	return (
		<svg
			xmlns="http://www.w3.org/2000/svg"
			className={[classes.AppSvg, className].join(' ')}
			aria-labelledby={`${iconName} icon`}
			viewBox={viewBox}
			width={width}
			height={height}
		>
			<use href={iconPath}></use>
		</svg>
	);
};

export default AppSvg;
