import { FC, useMemo } from 'react';
import { useSwipeable } from 'react-swipeable';

import BaseBtnAlpha from '../UI/buttons/BaseBtnAlpha/BaseBtnAlpha';
import BaseBtnBeta from '../UI/buttons/BaseBtnBeta/BaseBtnBeta';
import VolumeControl from '../UI/controls/VolumeControl/VolumeControl';
import PlayerControlsItem from './PlayerControlsItem';

import getImageSrc from '../../utils/getImageSrc';

import {
	TPlayerOptions,
	TVideoOptions,
	TSelectedVideoOptions,
	TSelectVideoOption,
	TSelectPlayerOption,
	TToggle,
} from '../../types/types';

import logo from '../../assets/png/logo.png';

export interface IPlayerControlsPaneProps {
	className: string;
	playerOptions: TPlayerOptions;
	videoOptions: TVideoOptions;
	selectedVideoOptions: TSelectedVideoOptions;
	selectVideoOption: TSelectVideoOption;
	selectPlayerOption: TSelectPlayerOption;
	toggleControls: TToggle;
}

const PlayerControlsPane: FC<IPlayerControlsPaneProps> = ({
	className,
	playerOptions,
	videoOptions,
	selectedVideoOptions,
	selectVideoOption,
	selectPlayerOption,
	toggleControls,
}) => {
	const swipeHandlers = useSwipeable({
		onSwipedRight: () => toggleControls(false),
		// Set duration in order to be able to drag volume control
		swipeDuration: 150,
	});

	const { qualityOptions, langOptions, avatarOptions } = useOptions({
		videoOptions,
		selectedVideoOptions,
		selectVideoOption,
	});

	const changeVolume = (value: number) => {
		selectPlayerOption({ name: 'volume', value });
	};

	return (
		<div
			className={['player-controls-pane', className].join(' ')}
			{...swipeHandlers}
		>
			<img
				src={logo}
				alt="Rufilms logo"
				className="player-controls-pane__logo"
				width={50}
				height={65}
			/>

			<div className="player-controls-pane__list">
				<PlayerControlsItem title="Video quality">
					{qualityOptions}
				</PlayerControlsItem>

				<PlayerControlsItem title="Volume">
					<VolumeControl
						value={playerOptions.volume}
						min={0}
						max={1}
						step={0.01}
						callOnInput={changeVolume}
					/>
				</PlayerControlsItem>

				<PlayerControlsItem title="Language">{langOptions}</PlayerControlsItem>

				<PlayerControlsItem title="Avatar">{avatarOptions}</PlayerControlsItem>
			</div>
		</div>
	);
};
export default PlayerControlsPane;

function useOptions({
	videoOptions,
	selectedVideoOptions,
	selectVideoOption,
}: {
	videoOptions: TVideoOptions;
	selectedVideoOptions: TSelectedVideoOptions;
	selectVideoOption: TSelectVideoOption;
}) {
	const qualityOptions = useMemo(
		() =>
			videoOptions.qualities?.map((option) => {
				return (
					<BaseBtnBeta
						key={option}
						aria={{ label: `${option} quality` }}
						active={option === selectedVideoOptions.quality}
						callOnClick={() =>
							selectVideoOption({ name: 'quality', value: option })
						}
					>
						{option}
					</BaseBtnBeta>
				);
			}),
		[selectedVideoOptions.quality]
	);

	const langOptions = useMemo(
		() =>
			videoOptions.langs?.map((option) => {
				return (
					<BaseBtnBeta
						key={option}
						aria={{ label: `${option} language` }}
						active={option === selectedVideoOptions.lang}
						callOnClick={() =>
							selectVideoOption({ name: 'lang', value: option })
						}
					>
						{option}
					</BaseBtnBeta>
				);
			}),
		[selectedVideoOptions.lang]
	);

	const avatarOptions = useMemo(
		() =>
			videoOptions.avatars?.map((option) => {
				return (
					<BaseBtnAlpha
						key={option}
						aria={{ label: `${option} avatar` }}
						callOnClick={() =>
							selectVideoOption({ name: 'avatar', value: option })
						}
						className={[
							'change-avatar-btn',
							option === selectedVideoOptions.avatar
								? 'change-avatar-btn--active'
								: '',
						].join(' ')}
					>
						<img
							className="avatar-img"
							src={getImageSrc(`jpg/avatars/${option.toLowerCase()}.jpg`)}
							alt={`${option} avatar`}
							width="80"
							height="80"
						/>
					</BaseBtnAlpha>
				);
			}),
		[selectedVideoOptions.avatar]
	);

	return {
		qualityOptions,
		langOptions,
		avatarOptions,
	};
}
