import { FC, useState, useEffect, useContext } from 'react';
import { CSSTransition } from 'react-transition-group';
import { useSwipeable } from 'react-swipeable';

import PlayerVideo from './PlayerVideo';
import PlayerControls from './PlayerControls';

import DeviceTypeContext from '../../contexts/DeviceTypeContext';
import getAspectRatio from '../../utils/getAspectRatio';
import getImageSrc from '../../utils/getImageSrc';
import Video from '../../api/Video';
import {
	TPlayerOptions,
	TSelectedVideoOptions,
	IVideoAttributes,
	TVideoOptions,
	TVideoDataState,
	TSelectVideoOption,
	TSelectPlayerOption,
} from '../../types/types';

export interface IPlayerProps {
	data: TVideoDataState;
	onPlayerLanguageChange: (lang: string) => void;
}

const Player: FC<IPlayerProps> = ({ data, onPlayerLanguageChange }) => {
	const [controlsActive, setControlsActive] = useState<boolean>(false);
	const [videoAttributes, setVideoAttributes] = useState<IVideoAttributes>({
		currentTime: 0,
		volume: 0.5,
		autoplay: true,
		controls: false,
		loop: false,
		playsinline: true,
		muted: false,
		src: '',
		poster: '',
	});
	const [saveCurrentTime, setSaveCurrentTime] = useState<boolean>(true);
	const [isPlayAgain, setIsPlayAgain] = useState<boolean>(false);

	const deviceType = useContext(DeviceTypeContext);
	const swipeHandlers = useSwipeable({
		onSwipedLeft: () => toggleControls(true),
	});

	const {
		videoOptions,
		playerOptions,
		setPlayerOptions,
		selectedVideoOptions,
		setSelectedVideoOptions,
		videoNameChanged,
		setVideoNameChanged,
	} = useOptions({ data, deviceType });

	useEffect(() => {
		if (videoOptions.videoName) {
			const src = Video.getVideoUrl({
				quality: selectedVideoOptions.quality,
				lang: selectedVideoOptions.lang,
				avatar: selectedVideoOptions.avatar,
				screen: selectedVideoOptions.screen,
				videoName: videoOptions.videoName,
				start: 0,
			});

			const aspectRatio = getAspectRatio({
				ratios: videoOptions.screens,
				deviceType,
			});

			const poster = getImageSrc(
				`jpg/posters/${aspectRatio}/${selectedVideoOptions.avatar?.toLowerCase()}.jpg`
			);

			saveVideoAttributes({
				src,
				poster,
			});
		}
	}, [
		selectedVideoOptions.quality,
		selectedVideoOptions.lang,
		selectedVideoOptions.avatar,
		videoOptions.videoName,
		deviceType,
	]);

	useEffect(() => {
		if (videoOptions.videoName) {
			const src = Video.getVideoUrl({
				quality: selectedVideoOptions.quality,
				lang: selectedVideoOptions.lang,
				avatar: selectedVideoOptions.avatar,
				screen: selectedVideoOptions.screen,
				videoName: videoOptions.videoName,
				start: 0,
			});

			if (src === videoAttributes.src) {
				setIsPlayAgain(true);
			} else {
				if (isPlayAgain) setIsPlayAgain(false);
			}
		}
	}, [data]);

	useEffect(() => {
		if (selectedVideoOptions.lang) {
			onPlayerLanguageChange(selectedVideoOptions.lang);
		}
	}, [data, selectedVideoOptions.lang]);

	function toggleControls(value: boolean) {
		setControlsActive(value);
	}

	function saveVideoAttributes(attrs: Partial<IVideoAttributes>) {
		if (attrs.currentTime && videoNameChanged) {
			setVideoNameChanged(false);
			return;
		}

		setVideoAttributes((prevState) => {
			return {
				...prevState,
				...attrs,
			};
		});
	}

	const selectVideoOption: TSelectVideoOption = ({ name, value }) => {
		if (!(name in selectedVideoOptions)) {
			return;
		}

		setSelectedVideoOptions((prevState) => {
			return {
				...prevState,
				[name]: value,
			} as TSelectedVideoOptions;
		});

		if (name === 'lang') {
			setSaveCurrentTime(false);
		} else {
			setSaveCurrentTime(true);
		}
	};

	const selectPlayerOption: TSelectPlayerOption = ({ name, value }) => {
		if (name in playerOptions) {
			setPlayerOptions((prevState) => {
				return {
					...prevState,
					[name]: value,
				} as TPlayerOptions;
			});
		}

		if (name in videoAttributes) {
			saveVideoAttributes({ [name]: value });
		}
	};

	// TODO: Optimize re-rendering after context changing via useMemo
	return (
		<div className="player" {...swipeHandlers}>
			<PlayerVideo
				attributes={videoAttributes}
				saveCurrentTime={saveCurrentTime}
				className="player__video"
				onSaveAttributes={saveVideoAttributes}
				isPlayAgain={isPlayAgain}
				setIsPlayAgain={setIsPlayAgain}
			/>
			<CSSTransition
				in={controlsActive}
				timeout={0}
				classNames="slide-animation"
				// Resolve CSSTransition 'findDOMNode is deprecated in StrictMode' warning
			>
				<PlayerControls
					className={['player__controls'].join(' ')}
					controlsActive={controlsActive}
					toggleControls={toggleControls}
					playerOptions={{
						volume: playerOptions.volume,
					}}
					videoOptions={{
						qualities: videoOptions.qualities,
						avatars: videoOptions.avatars,
						langs: videoOptions.langs,
						// TODO: Remove unnecessary object field 'screens'
						screens: videoOptions.screens,
					}}
					selectedVideoOptions={selectedVideoOptions}
					selectVideoOption={selectVideoOption}
					selectPlayerOption={selectPlayerOption}
				/>
			</CSSTransition>
		</div>
	);
};

export default Player;

function useOptions({
	data,
	deviceType,
}: {
	data: TVideoDataState;
	deviceType: string;
}) {
	const [playerOptions, setPlayerOptions] = useState<TPlayerOptions>({});
	const [videoOptions, setVideoOptions] = useState<TVideoOptions>({});
	const [selectedVideoOptions, setSelectedVideoOptions] =
		useState<TSelectedVideoOptions>({});
	const [videoNameChanged, setVideoNameChanged] = useState<boolean>(false);

	useEffect(() => {
		if (Object.keys(data).length) {
			initOptions(data);
		}
	}, [data]);

	useEffect(() => {
		setSelectedVideoOptions((prevState) => {
			return {
				...prevState,
				screen: getAspectRatio({ ratios: videoOptions.screens, deviceType }),
			} as TSelectedVideoOptions;
		});
	}, [deviceType]);

	function initOptions(data: TVideoDataState) {
		if (videoOptions?.videoName !== data.video_name) {
			setVideoNameChanged(true);
		} else {
			setVideoNameChanged(false);
		}

		setVideoOptions({
			qualities: data.qualities || [],
			langs: data.lang || [],
			avatars: data.avatars || [],
			screens: data.screens || [],
			videoName: data.video_name || '',
		});

		const selectedQualiity = selectedVideoOptions.quality;
		const selectedAvatar = selectedVideoOptions.avatar;
		const selectedLang = selectedVideoOptions.lang;

		const selectedQualityInData = data.qualities?.includes(selectedQualiity);
		const selectedAvatarInData = data.avatars?.includes(selectedAvatar);
		const selectedLangInData = data.lang?.includes(selectedLang);

		if (selectedQualityInData || selectedAvatarInData || selectedLangInData) {
			setSelectedVideoOptions({
				quality: selectedQualityInData ? selectedQualiity : data.qualities[0],
				lang: selectedLangInData ? selectedLang : data.lang[0],
				avatar: selectedAvatarInData ? selectedAvatar : data.avatars[0],
				screen: getAspectRatio({ ratios: data.screens, deviceType }),
			});
		} else {
			setSelectedVideoOptions({
				quality: data.qualities ? data.qualities[0] : '',
				lang: data.lang ? data.lang[0] : '',
				avatar: data.avatars ? data.avatars[0] : '',
				screen: getAspectRatio({ ratios: data.screens, deviceType }),
			});
		}

		setPlayerOptions({
			volume: playerOptions?.volume || 0.5,
			duration: data.durability || 0,
		});
	}

	return {
		playerOptions,
		setPlayerOptions,
		videoOptions,
		setVideoOptions,
		selectedVideoOptions,
		setSelectedVideoOptions,
		videoNameChanged,
		setVideoNameChanged,
	};
}
