import axios from 'axios';

import { BASE_URL, HEADERS } from './config';
import {
	TVideoData,
	TMessageBody,
	TScriptMessageBody,
	TUserData,
} from './types';

export default class Message {
	static async sendGreetingMessage(): Promise<TVideoData> {
		return axios
			.get(`${BASE_URL}message/greeting/`)
			.then((response) => {
				console.log('sendGreetingMessage success: ', response.data);
				return response.data;
			})
			.catch((err) => console.log('sendGreetingMessage error: ', err));
	}

	static async sendAuthMessage(): Promise<TVideoData> {
		return axios
			.get(`${BASE_URL}message/auth/`)
			.then((response) => {
				console.log('sendAuthMessage success: ', response.data);
				return response.data;
			})
			.catch((err) => console.log('sendAuthMessage error: ', err));
	}

	static async sendMessage({ msg }: TMessageBody): Promise<TVideoData> {
		return axios
			.post(
				`${BASE_URL}message/`,
				{
					msg,
				},
				{
					headers: HEADERS,
				}
			)
			.then((response) => {
				console.log('sendMessage success: ', response);
				return response.data;
			})
			.catch((err) => console.log('sendMessage error: ', err));
	}

	static async sendScriptMessage({
		msg,
		pk,
		position,
	}: TScriptMessageBody): Promise<TVideoData> {
		return axios
			.post(
				`${BASE_URL}message/script/`,
				{
					msg,
					pk,
					position,
				},
				{
					headers: HEADERS,
				}
			)
			.then((response) => {
				console.log('sendScriptMessage success: ', response);
				return response.data;
			})
			.catch((err) => console.log('sendScriptMessage error: ', err));
	}

	static async sendUserData({ name, email }: TUserData): Promise<null> {
		return axios
			.post(
				`${BASE_URL}message/auth/`,
				{
					name,
					email,
				},
				{
					headers: HEADERS,
				}
			)
			.then((response) => {
				console.log('sendUserData success: ', response);
				return response.data;
			})
			.catch((err) => console.log('sendUserData error: ', err));
	}
}
