import { FC, useEffect, useRef, useState } from 'react';
import { CSSTransition } from 'react-transition-group';
import { useSwipeable } from 'react-swipeable';

import ChatMessage from './ChatMessage';
import AppSvg from '../UI/AppSvg/AppSvg';

import { IMessage } from '../../types/types';

import spritePath from '../../assets/svg/sprite.svg';
export interface IChatHistoryProps {
	className?: string;
	messages: IMessage[];
}

const ChatHistory: FC<IChatHistoryProps> = ({ className, messages }) => {
	// Add swipeDownHandler handler in order to prevent chat disappearing
	const swipeDownHandler = useSwipeable({
		onSwipedDown: ({ event }) => event.stopPropagation(),
	});

	const [historyActive, setHistoryActive] = useState<boolean>(true);
	const messagesListRef = useRef<HTMLDivElement>(null);

	useEffect(() => {
		const el = messagesListRef.current;

		if (el) {
			const clientHeight = el.clientHeight;
			const scrollHeight = el.scrollHeight;
			const diff = scrollHeight - clientHeight;

			if (diff) el.scrollTop = diff;
		}
	}, [messages]);
	return (
		<div
			{...swipeDownHandler}
			className={['chat-history', className].join(' ')}
		>
			<CSSTransition
				in={historyActive}
				timeout={0}
				classNames={'fade-animation'}
			>
				<button
					className="chat-history__toggle"
					onClick={() => setHistoryActive(!historyActive)}
				>
					<span>{historyActive ? 'Hide history' : 'Show history'}</span>

					<AppSvg
						iconName={
							historyActive ? 'toggle-arrow-bottom' : 'toggle-arrow-top'
						}
						viewBox="-1 -4 25 20"
						width="25"
						height="20"
						spritePath={spritePath}
					/>
				</button>
			</CSSTransition>

			<CSSTransition
				in={historyActive}
				timeout={0}
				classNames="popup-animation"
			>
				<div
					ref={messagesListRef}
					className={[
						'chat-history__messages-list',
						historyActive ? 'chat-history__messages-list--active' : '',
					].join(' ')}
				>
					{messages.map((message, id) => (
						<ChatMessage
							className={[
								'chat-history__message',
								message.fromMe ? 'chat-history__message--from-me' : '',
							].join(' ')}
							message={message}
							key={id}
						/>
					))}
				</div>
			</CSSTransition>
		</div>
	);
};

export default ChatHistory;
