import React, { FC } from 'react';
import { IAria } from '../../../../types/types';

import classes from './BaseBtnBeta.module.scss';

export interface IBaseBtnBetaProps {
	children: React.ReactNode;
	callOnClick: (e: any) => void;
	callOnMouseDown?: (e: any) => void;
	aria: IAria;
	active?: boolean;
	className?: string;
	type?: 'button' | 'submit' | 'reset';
}

const BaseBtnBeta: FC<IBaseBtnBetaProps> = (
	{ children, callOnClick, callOnMouseDown, aria, active, className, type } = {
		children: '',
		callOnClick: () => null,
		callOnMouseDown: () => null,
		active: false,
		className: '',
		aria: {
			label: 'Button',
			haspopup: false,
			expanded: false,
		},
	}
) => {
	return (
		<button
			onClick={callOnClick}
			onMouseDown={callOnMouseDown}
			className={[
				classes.BaseBtnBeta,
				active ? classes.BaseBtnBetaActive : '',
				className,
			].join(' ')}
			aria-label={aria.label}
			aria-haspopup={aria.haspopup}
			aria-expanded={aria.expanded}
			type={type}
		>
			{children}
		</button>
	);
};

export default BaseBtnBeta;
