import { FC } from 'react';

import { IMessage } from '../../types/types';

import getLinksFromText from '../../utils/getLinksFromText';
import getTextWithClickableLink from '../../utils/getTextWithClickableLink';
export interface IChatMessageProps {
	className?: string;
	message: IMessage;
}

const ChatMessage: FC<IChatMessageProps> = (
	{ className, message } = {
		className: '',
		message: {
			fromMe: false,
			text: '',
		},
	}
) => {
	const getText = (text: string) => {
		const urls = getLinksFromText(text);
		const newText = getTextWithClickableLink({ text, urls });

		return newText;
	};

	return (
		<div
			dangerouslySetInnerHTML={getText(message.text)}
			className={[
				'chat-message',
				message.fromMe ? 'chat-message--from-me' : '',
				className,
			].join(' ')}
		/>
	);
};

export default ChatMessage;
