const getTextWithClickableLink = ({
	text,
	urls,
}: {
	text: string;
	urls: string[];
}): { __html: string } => {
	let newText = '';

	urls.forEach((url) => {
		newText = text.replaceAll(
			url,
			`<a class="product-remark__link" href="${url}" target="_blank">${url}</a>`
		);
	});
	return {
		__html: newText,
	};
};

export default getTextWithClickableLink;
