import { FC, useState } from 'react';

import ChatActions from './ChatActions';
import ChatVoice from './ChatVoice';
import BaseTextInput from '../UI/inputs/BaseTextInput/BaseTextInput';
import BaseBtnBeta from '../UI/buttons/BaseBtnBeta/BaseBtnBeta';

export interface IChatFormMessageProps {
	actions: string[];
	onSend: (message: string) => void;
}

const ChatFormMessage: FC<IChatFormMessageProps> = ({ actions, onSend }) => {
	const [message, setMessage] = useState<string>('');

	const sendMessage = () => {
		if (!message.trim()) {
			return;
		}

		onSend(message);
		setMessage('');
	};

	const sendAction = (action: string) => {
		onSend(action);
	};

	return (
		<form onSubmit={(e) => e.preventDefault()} className="chat-form-message">
			{!!actions.length && (
				<ChatActions actions={actions || []} sendAction={sendAction} />
			)}

			<div className="chat-form-message__container">
				<BaseTextInput
					value={message}
					autoFocus={true}
					required={false}
					callOnChange={(value) => setMessage(value)}
					className="chat-form-message__text-input"
					type="text"
					placeholder="Type your message"
				/>

				{/* <ChatVoice setMessage={setMessage} /> */}
			</div>

			<BaseBtnBeta
				type="submit"
				callOnMouseDown={(e) => e.preventDefault()}
				callOnClick={sendMessage}
				className="chat-form-message__send-btn"
				aria={{
					label: 'Send message',
					haspopup: false,
					expanded: false,
				}}
			>
				Send
			</BaseBtnBeta>
		</form>
	);
};

export default ChatFormMessage;
